import React, { Component } from 'react';

import './App-page-one.css';
import Play from './app-page-one-play.png';

class AppPageOne extends Component {
  render() {
    return (
      <div className="App-page App-page-one animated fadeInLeft">
        <div className="App-page__content App-page-one__content">
          <h1 className="App-page-one__item"> <br />   <br /><span> <br />  <br /> </span></h1>
          <p className="App-page-one__item"> 45    <br /></p>
          <div className="App-page-one__item">
            <button className="Button ButtonWhite" href="#"> </button>
          </div>
          <p className="App-page-one__item"> PACIFIC ASIA GROUP  2007         .</p>
          <div className="App-page-one__item">
            <div className="App-page-one__item__play-video" onMouseEnter={this.props.play} onMouseLeave={this.props.pause} onClick={this.props.playerOpen}>
              <div className="App-page-one__item__img">
                <img src={Play} alt="Play" />
              </div>
              <div className="App-page-one__item__text"> </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AppPageOne;


