import React, { Component } from 'react';
import $ from 'jquery';
import './App-page-two.css';
import '../App-pages.css';
import Bitmap from './Bitmap.png';
import LeftArrow from './left-arrow.png';
import RightArrow from './right-arrow.png';

class TabBasic extends Component {

  constructor(props) {
   super(props);
    this.bitmapAnimate = this.bitmapAnimate.bind(this);
  }

  bitmapAnimate() {
    $('#Tab-basic-top__bitmap').addClass('bitmapAnimate')
  }

  render() {
    return (
      <div className="Tab-basic animated fadeInRight" onMouseOver={this.bitmapAnimate}>
        <div className="Tab-basic-top">
          <h4 className="Tab-basic-top__item">  <br />  </h4>
          <p className="Tab-basic-top__item">                    .</p>
          <img className="Tab-basic-top__item" id="Tab-basic-top__bitmap" src={Bitmap} alt="" />
          <div className="Tab-basic-top__item">
            <div className="Tab-basic-top-arrow-left"><img src={LeftArrow} alt="" /></div>
            <div className="Tab-basic-top-arrow-right"><img src={RightArrow} alt="" /></div>
          </div>
        </div>
        <div className="Tab-basic-bottom">
          <div className="Tab-basic-bottom__item">
            <button className="Button ButtonBlack" href="#"></button>
          </div>
          <p className="Tab-basic-bottom__item">    <span>400 .</span></p>
          <p className="Tab-basic-bottom__item">   <span>45-50 </span></p>
          <p className="Tab-basic-bottom__item">   <span>0,9$/.</span></p>
          <div className="Tab-basic-bottom__item">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </div>

      );
  }
};

class TabAdditional extends Component {
  render() {
    return (
      <div className="Tab-additional animated fadeInLeft">
        <div className="Tab-additional__item">
          <h6> <br />  <br /> </h6>
          <p>        :    Pacific Asia Group.</p>
        </div>
        <div className="Tab-additional__item">
          <h6> <br /></h6>
          <p>   ? -          .</p>
        </div>
        <div className="Tab-additional__item">
          <h6> <br /> <br /> </h6>
          <p>      .        ,     ,     .</p>
        </div>
        <div className="Tab-additional__item">
          <h6> <br /> </h6>
          <p>                 .</p>
        </div>
        <div className="Tab-additional__item">
          <h6> <br /> </h6>
          <p>                             .</p>
        </div>
        <div className="Tab-additional__item">
          <h6> <br /> </h6>
          <p>  Pacific Asia Group        .</p>
        </div>
      </div>
    );
  }
};

class AppPageTwo extends Component {
  constructor(props) {
      super(props);
      this.state = {
        tabContent: <TabBasic />
      };
      this.tabBasic = this.tabBasic.bind(this);
      this.tabAdditional = this.tabAdditional.bind(this);
    }

    tabBasic() {
      this.setState({
        tabContent: <TabBasic />
      });
      $('.App-page-two-top-tabs').removeClass('App-page-two-top-tabs-additional');
      $('.App-page-two-top-tabs').addClass('App-page-two-top-tabs-basic');
      setTimeout(function() {
        $('#Tab-basic-top__bitmap').addClass('bitmapAnimate');
      }, 300);
      
    }
    tabAdditional() {
      this.setState({
        tabContent: <TabAdditional />
      });
      $('.App-page-two-top-tabs').removeClass('App-page-two-top-tabs-basic');
      $('.App-page-two-top-tabs').addClass('App-page-two-top-tabs-additional');
    }

  render() {
    return (
      <div className="App-page App-page-two">
      	<div className="App-page-two__top">
      		<h2> <br /></h2>
          <div className="App-page-two-top-tabs App-page-two-top-tabs-basic">
            <div className="App-page-two-top-tabs__button" onClick={this.tabBasic}></div>
            <div className="App-page-two-top-tabs__button" onClick={this.tabAdditional}></div>
          </div>
      	</div>
        <div className="App-page-two__bottom">
          {this.state.tabContent}
        </div>
      </div>
    );
  }
}

export default AppPageTwo;


